import React, { useState, useRef } from 'react';
import { IoMdCheckmark } from "react-icons/io";
import { useNavigate } from 'react-router-dom';

import './signup.scss';

const SignUpForm = () => {
    const history=useNavigate();

    const idInputRef = useRef(null);
    const pwInputRef = useRef(null);
    const nameInputRef = useRef(null);
    const phoneInputRef = useRef(null);
    const emailInputRef = useRef(null);
    const birthInputRef = useRef(null);

    const [id, setId] = useState('');
    const [pw, setPw] = useState('');
    const [pw2, setPw2] = useState('');
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');
    const [email, setEmail] = useState('');
    const [birth, setBirth] = useState('');

    const [allChecked, setAllChecked]=useState(false);
    const [termsChecked, setTermsChecked]=useState(false);
    const [privacyChecked, setPrivacyChecked]=useState(false);
    const [snsChecked, setSnsChecked]=useState(false);

    const idRule = /^[a-z0-9]{4,16}$/;
    const pwRule = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[!@#$%^&*])[a-zA-Z\d!@#$%^&*]{8,16}$/;
    const nameRule = /^[a-zA-Z-]{1,20}$/;
    const phoneRule = /^\d{8}$/;
    const emailRule = /^[0-9a-zA-z]([-_\.]?[0-9a-zA-z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
    const birthRule = /^(19[0-9][0-9]|20\d{2})(0[0-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])$/;




    const [messages, setMessages] = useState({
        id: { text: '', color: '' },
        pw: { text: '', color: '' },
        pw2: { text: '', color: '' },
        name: { text: '', color: '' },
        phone: { text: '', color: '' },
        email: { text: '', color: '' },
        birth: { text: '', color: '' },
    });

    const handleMessageChange = (key, text, color) => {
        setMessages((prevMessages) => ({
            ...prevMessages,
            [key]: { text, color }
        }))
    }

    const handleId = (event) => {
        const newValue = event.target.value;
        setId(newValue)
        if (idRule.test(newValue)) {
            handleMessageChange('id', '  .', 'success-color');
        } else if (newValue === "") {
            handleMessageChange('id', ' ', 'error-color');
        } else {
            handleMessageChange('id', '  / 4 .', 'error-color');
            setId('');
        }
    }

    const handlePw = (event) => {
        const newPwValue = event.target.value;
        setPw(newPwValue)
        if (pwRule.test(newPwValue)) {
            handleMessageChange('pw', '  .', 'success-color');
        } else if (newPwValue === "") {
            handleMessageChange('pw', ' ', 'error-color');
        } else {
            handleMessageChange('pw', ' // 8~16 .', 'error-color');
            setPw('');
        }
    }

    const handlePw2 = (event) => {
        const newPw2Value = event.target.value;
        setPw2(newPw2Value)
        if (pw === "") { //  
            handleMessageChange('pw', ' ', 'error-color');
        } else if (newPw2Value === pw) {
            handleMessageChange('pw2', ' .', 'success-color');
        } else if (newPw2Value === "") {
            handleMessageChange('pw2', '  ', 'error-color');
            setPw2('');
        } else {
            handleMessageChange('pw2', '  ', 'error-color');
            setPw2('');
        }
    }

    const handleName = (event) => {
        const newNameValue = event.target.value;
        setName(newNameValue)
        if (nameRule.test(newNameValue)) {
            handleMessageChange('name', '  .', 'success-color');
        } else if (newNameValue === "") {
            handleMessageChange('name', ' ', 'error-color');
        } else {
            handleMessageChange('name', ' / 1 .', 'error-color');
            setName('');
        }
    }

    const handlePhone = (event) => {
        const newPhoneValue = event.target.value;
        setPhone(newPhoneValue)
        if (phoneRule.test(newPhoneValue)) {
            handleMessageChange('phone', '  .', 'success-color');
        } else if (newPhoneValue === "") {
            handleMessageChange('phone', ' ', 'error-color');
        } else {
            handleMessageChange('phone', '  .', 'error-color');
            setPhone('');
        }
    }

    const handleEmail = (event) => {
        const newEmailValue = event.target.value;
        setEmail(newEmailValue)
        if (emailRule.test(newEmailValue)) {
            handleMessageChange('email', '  .', 'success-color');
        } else if (newEmailValue === "") {
            handleMessageChange('email', ' ', 'error-color');
        } else {
            handleMessageChange('email', '   .', 'error-color');
            setEmail('');
        }
    }

    const handleBirth = (event) => {
        const newBirthValue = event.target.value;
        setBirth(newBirthValue)
        if (birthRule.test(newBirthValue)) {
            handleMessageChange('birth', '  .', 'success-color');
        } else if (newBirthValue === "") {
            handleMessageChange('birth', '  ', 'error-color');
        } else {
            handleMessageChange('birth', '   .', 'error-color');
            setBirth('');
        }
    }

    const handleAllcheck = () => {
        setAllChecked(!allChecked);
        setTermsChecked(!termsChecked);
        setPrivacyChecked(!privacyChecked);
        setSnsChecked(!snsChecked);
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        if(
            idRule.test(id) &&
            pwRule.test(pw) &&
            pw2===pw &&
            nameRule.test(name) &&
            phoneRule.test(phone) &&
            emailRule.test(email) &&
            birthRule.test(birth) &&
            termsChecked &&
            privacyChecked &&
            snsChecked
        ){
            history("/");
        }else{
            console.log('error');
            
        }
    }

    return (
        <div className='signWrap'>
            <h2></h2>
            <fieldset className="signUpArea">
                <form action="#" method='post' name='signup' onSubmit={handleSubmit}>
                    <ul>
                        <li className="id-section">
                            <div className="area-style">
                                <label htmlFor="idArea" className='label-style'></label>
                                <input ref={idInputRef} type="text" id='idArea' required size={20} value={id} onChange={(event) => { setId(event.target.value) }} onBlur={handleId} />
                                <span className={`mes-style ${messages.id.color}`}>
                                    {messages.id.text}
                                </span>
                                <p className="help-style"><IoMdCheckmark />/, 4-16</p>
                            </div>
                        </li>
                        <li className="pw-section">
                            <div className="area-style">
                                <label htmlFor="pwArea" className='label-style'></label>
                                <input ref={pwInputRef} type="text" id='pwArea' required size={20} value={pw} onChange={(event) => { setPw(event.target.value) }} onBlur={handlePw} />
                                <span className={`mes-style ${messages.pw.color}`}>
                                    {messages.pw.text}
                                </span>
                                <p className="help-style"><IoMdCheckmark />//, 8-16</p>
                                <br />

                                <label htmlFor="pw2Area" className='label-style'></label>
                                <input type="text" id='pw2Area' required size={20} value={pw2} onChange={(event) => { setPw2(event.target.value) }} onBlur={handlePw2} />
                                <span className={`mes-style ${messages.pw2.color}`}>
                                    {messages.pw2.text}
                                </span>
                            </div>
                        </li>
                        <li className="name-section">
                            <div className="area-style">
                                <label htmlFor="nameArea" className='label-style'></label>
                                <input ref={nameInputRef} type="text" id='nameArea' required size={20} value={name} onChange={(event) => { setName(event.target.value) }} onBlur={handleName} />
                                <span className={`mes-style ${messages.name.color}`}>
                                    {messages.name.text}
                                </span>
                            </div>
                        </li>
                        <li className="phone-section">
                            <div className="area-style">
                                <label htmlFor="phoneArea" className='label-style'></label>
                                <div className='flexBox'>
                                    <select className="select-style" id="phoneNumber">
                                        <option value="010">010</option>
                                        <option value="011">011</option>
                                        <option value="019">019</option>
                                        <option value="018">018</option>
                                        <option value="016">016</option>
                                    </select>
                                    <span>&nbsp;&nbsp;</span>
                                    <input ref={phoneInputRef} type="text" id='phoneArea' required size={20} value={phone} onChange={(event) => { setPhone(event.target.value) }} onBlur={handlePhone} />
                                </div>
                                <span className={`mes-style ${messages.phone.color}`}>
                                    {messages.phone.text}
                                </span>

                            </div>
                        </li>
                        <li className="email-section">
                            <div className="area-style">
                                <label htmlFor="emailArea" className='label-style'></label>
                                <input ref={emailInputRef} type="text" id='emailArea' required size={20} value={email} onChange={(event) => { setEmail(event.target.value) }} onBlur={handleEmail} />
                                <span className={`mes-style ${messages.email.color}`}>
                                    {messages.email.text}
                                </span>
                            </div>
                        </li>
                        <li className="birth-section">
                            <div className="area-style">
                                <label htmlFor="birthArea" className='label-style'></label>
                                <input ref={birthInputRef} type="text" id='birthArea' required size={8} value={birth} onChange={(event) => { setBirth(event.target.value) }} onBlur={handleBirth} />
                                <span className={`mes-style ${messages.birth.color}`}>
                                    {messages.birth.text}
                                </span>
                                <p className="help-style"><IoMdCheckmark />.   8 ex)19900101 </p>
                            </div>
                        </li>
                        <li>
                            <br />
                            <hr />
                            <br />
                        </li>
                        <li id="terms-section">
                            <input type="checkbox" id="allCheck" className='check-style' checked={allChecked} onChange={handleAllcheck}  />
                            <label htmlFor="allCheck">    , ()  .</label>
                            <br />
                            <h3>[]  </h3>
                            <div className="termsBox">
                                <p>
                                       
                                    <br />
                                     , ,         .
                                </p>
                                <p>
                                      :  ,  ,  , ID ,  ,    ,   ,   ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,   ,   ,  IP  , 
                                    <br />
                                       : () , 
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                          .
                                </p>
                                <p>
                                                 ,     ,     
                                    <br />
                                      
                                    <br />
                                         ,   ,  , 14          ,       
                                    <br />
                                            
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                                 .
                                </p>
                            </div>
                            <p>
                                <span> ?</span>
                                <input type="checkbox" id="terms-check1" className='check-style' checked={termsChecked} onChange={()=>setTermsChecked(!termsChecked)}/>
                                <label htmlFor="terms-check1"></label>
                            </p>
                            <p>
                                <h3>[]     </h3>
                            </p>
                            <div className="termsBox">
                                <p>
                                       
                                    <br />
                                     , ,         .
                                </p>
                                <p>
                                      :  ,  ,  , ID ,  ,    ,   ,   ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,   ,   ,  IP  , <br />
                                       : () , 
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                          .
                                </p>
                                <p>
                                                 ,     ,     <br />
                                      
                                         ,   ,  , 14          ,  <br />     <br />
                                            
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                                 .
                                </p>
                            </div>
                            <p>
                                <span>    ?</span>                                
                                <input type="checkbox" id="terms-check2" className='check-style' checked={privacyChecked} onChange={()=>setPrivacyChecked(!privacyChecked)}/>
                                <label htmlFor="terms-check2"></label>
                            </p>
                            <h3>[]  </h3>
                            <div className="termsBox">
                                <p>
                                       <br />
                                     , ,         .
                                </p>
                                <p>
                                      :  ,  ,  , ID ,  ,    ,   ,   ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,   ,   ,  IP  , <br />
                                       : () , 
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                          .
                                </p>
                                <p>
                                                 ,     ,     <br />
                                      <br />
                                         ,   ,  , 14          ,   <br />
                                        <br />
                                            
                                </p>
                                <p>
                                        
                                </p>
                                <p>
                                                 .
                                </p>
                            </div>
                            <p>
                                <span>SMS  ?</span>                                
                                <input type="checkbox" id="terms-check3" className='check-style' checked={snsChecked} onChange={()=>setSnsChecked(!privacyChecked)}/>
                                <label htmlFor="terms-check3"></label>
                            </p>
                        </li>
                        <li className="submit-section">
                            <button className="submit-style" type="submit"></button>
                        </li>
                    </ul>
                </form>
            </fieldset>
        </div>
    );
};

export default SignUpForm;